#nav-bar {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#nav-bar .divider {
  justify-content: center;
  border-color: black;
  border-right-width: 0.2rem;
  height: 10rem;
}

#nav-bar-stack .tag {
  font-size: 1rem;
  font-family: "Courier New", Courier, monospace;
  text-transform: lowercase;
  color: black;
  display: block;
  position: relative;
  cursor: pointer;

  font-size: 1rem;
  text-align: center;
  transition: all 0.2s ease;
  &:before,
  &:after {
    content: "<loading...>";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    -webkit-clip: rect(0px, 0px, 0px, 0px);
    clip: rect(0px, 0px, 0px, 0px);
    background: #fff;
    color: #000;
  }

  &:before {
    left: -3px;
    top: -2px;
    text-shadow: 2px 0 #000;
    box-shadow: 2px 0 #efefef;
  }

  &:after {
    left: 2px;
    bottom: -2px;
    text-shadow: -1px 0 #000;
    box-shadow: -1px 0 #efefef;
  }

  &:hover {
    font-weight: bold;
    background: black;
    color: white;
  }

  &:hover:before {
    animation: glitch-test 1.5s infinite linear alternate-reverse;
  }

  &:hover:after {
    animation: glitch-test 2s infinite linear alternate;
  }
}

@keyframes glitch-test {
  0% {
    clip: rect(-3px, 600px, 0px, 0px);
  }
  5.88235% {
    clip: rect(0px, 600px, 0px, 0px);
  }
  11.76471% {
    clip: rect(-3px, 600px, 0px, 0px);
  }
  17.64706% {
    clip: rect(-3px, 600px, 0px, 0px);
  }
  23.52941% {
    clip: rect(100px, 600px, 100px, 0px);
  }
  29.41176% {
    clip: rect(0px, 600px, 600px, 0px);
  }
  35.29412% {
    clip: rect(100px, 600px, 0px, 0px);
  }
  41.17647% {
    clip: rect(0px, 600px, 600px, 0px);
  }
  47.05882% {
    clip: rect(100px, 600px, 0px, 0px);
  }
  52.94118% {
    clip: rect(-3px, 600px, 0px, 0px);
  }
  58.82353% {
    clip: rect(100px, 450px, 100px, 0px);
  }
  64.70588% {
    clip: rect(0px, 450px, 0px, 0px);
  }
  70.58824% {
    clip: rect(100px, 450px, 100px, 0px);
  }
  76.47059% {
    clip: rect(0px, 450px, 0px, 0px);
  }
  82.35294% {
    clip: rect(0px, 450px, 0px, 0px);
  }
  88.23529% {
    clip: rect(0px, 450px, 0px, 0px);
  }
  94.11765% {
    clip: rect(0px, 450px, 0px, 0px);
  }
  100% {
    clip: rect(0px, 450px, 0px, 0px);
  }
}
